// ** Custom Components
import Avatar from '@components/avatar'
// ** Reactstrap Imports
import { Badge } from 'reactstrap'
import moment from 'moment'

const renderClient = row => {

  return (
    <Avatar
      initials
      className='me-1'
      color={'light-primary'}
      content={row.contactname.charAt(0) || ''}
    />
  )

}

const statusObj = [
  '',
  'light-primary',
  'light-success',
  'light-danger'
]

const statusArr = [
  '',
  "Active",
  "Inactive",
  "Expired"
]

const dateFormat = (value) => {
  if (value.length === 13) {
    return moment(value, 'x').format("MMM DD, YYYY")
  } else {
    return moment.unix(value).format("MMM DD, YYYY")
  }

}

export const columns = [
  {
    name: 'Client',
    sortable: true,
    minWidth: '300px',
    sortField: 'client',
    selector: row => row.name,
    cell: row => (
      <div className='d-flex justify-content-left align-items-center'>
        {renderClient(row)}
        <div className='d-flex flex-column'>
          <small className='text-truncate mb-0'>{row.contactname}</small>
        </div>
      </div>
    )
  },
  {
    name: 'License Name',
    sortable: true,
    minWidth: '300px',
    sortField: 'client',
    cell: row => (
      <span className='fw-bolder'>{row.name}</span>
    )
  },
  {
    name: 'License NO',
    sortable: true,
    minWidth: '300px',
    sortField: 'client',
    cell: row => (
      <small className='text-truncate mb-0'>{row.licenseno}</small>
    )
  },
  {
    name: 'Issued Date',
    sortable: true,
    minWidth: '172px',
    sortField: 'role',
    selector: row => row.issueddate,
    cell: row => <span className='text-capitalize'>{dateFormat(row.issueddate)}</span>
  },
  {
    name: 'Expiry Date',
    minWidth: '138px',
    sortable: true,
    sortField: 'currentPlan',
    selector: row => row.expirydate,
    cell: row => <span className='text-capitalize'>{dateFormat(row.expirydate)}</span>
  },
  {
    name: 'Status',
    minWidth: '138px',
    sortable: true,
    sortField: 'status',
    selector: row => row.status,
    cell: row => (
      <Badge className='text-capitalize' color={statusObj[row.status]} pill>
        {statusArr[row.status]}
      </Badge>
    )
  }
]
